function state2Type(state: number): string {
  if (state == 1 || state == 2) {
    return "";
  } else if (state == 3 || state == 5 || state == 8) {
    return "success";
  } else if (state == 4 || state == 7) {
    return "warning";
  } else {
    return "danger";
  }
}

